.link{
  text-decoration:none;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 14px;
  border-bottom: 1px solid var(--notheme-color-accent);
  color: var(--notheme-color-accent);
  text-shadow: 0px 47px 355px rgba(0, 0, 0, 0.07),
    0px 9.4px 57.6875px rgba(0, 0, 0, 0.035);
  &:hover,
  :focus {
    color: var(--notheme-textColor-primary);
    border-color: var(--notheme-textColor-primary);
  }

  @media (min-width: 768px) {
    font-size: 18px;
    line-height: 24px;
  }
}